@import url(https://fonts.googleapis.com/css2?family=PT+Mono&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.1/css/bulma.min.css);
@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.1/css/bulma.min.css);
body {
  margin: 0;
  font-family: "PT Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

.OutputSectionMain {
  font-family: "PT Mono", monospace;
}

.images {
  max-width: "450px";
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn {
  margin-right: 30px;
  margin-top: 10px;
}

.OutputSection {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  /* width: 450px; */
}

.cardd {
  width: 450px;
}

@media screen and (max-width: 800px) {
  .OutputSection {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

input[type="range"] {
  width: 400px;
}

.container {
  /* transform: translateY(-100%); */
  position: fixed;
  top: 5rem;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-y: hidden;
}

.uploadedImage {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-self: center;
          align-self: center;
  width: 50px;
  height: 50px;
}

.drop-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  height: 100%;
  width: 100%;
}

.drop-message {
  text-align: center;
  color: black;
}

.add-border {
  border: 2px solid green;
}

.sampleImage {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 30px;
}

.image-bg {
  background-color: #e7f3f8;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transform: rotateZ(8.1deg);
          transform: rotateZ(8.1deg);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 700px) {
  .image-bg {
    display: none;
  }
}

.bg {
  margin: 3rem 2rem;
  padding: 3rem 2rem;
  background-color: rgb(220, 223, 228);
  border-radius: 10px;
}

.imageCard {
  margin: 20px;
}

.image {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sampleImageText {
  font-size: 1.2rem;
}

.close {
  cursor: pointer;
  position: relative;
  top: -70px;
  left: -50px;
  background-color: white;
  /* border-radius: 50px; */
  width: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
  border: 3px solid rgba(0, 0, 0, 0.5);
  font-weight: bolder;
  font-size: 20px;
}

